import React from "react"
import Post from './Post'

import { useStaticQuery, graphql } from 'gatsby'
import './style.scss'

const Blog = () => {
  const { blogs } = useStaticQuery(
    graphql`
      query {
        blogs: allMdx(
          filter: {frontmatter: {category: {eq: "blog"}, published: {eq: true}}}
          sort: {fields: frontmatter___date, order: DESC}
        ) {
          edges {
            node {
              id
              frontmatter {
                slug
                title
                image {
                  childImageSharp {
                    fluid(maxWidth: 800) {
                      ...GatsbyImageSharpFluid
                    }
                  }
                }
                date
                author
              }
            }
          }
        }
      }
    `
  )

  const blogsContent = blogs.edges;

  return (
    <section id="blog">
      <div className="container">
        <div className="text-center justify-content-center row mb-5">
          <div className="col-12 mb-5">
            <h3 className="heading"><span className="text-primary">Tinkers</span> Blog.</h3>
          </div>
          {
            blogsContent.map(item => {
              const {
                id,
                frontmatter: { slug, title, image, date, author }
              } = item.node;

              return (<Post key={id} slug={slug} title={title} src={image.childImageSharp.fluid.src} date={date} author={author} />)
            })
          }
        </div>
      </div>
    </section>
  )
}

export default Blog